/* 1.4 */
/* Typography (section title, links, page dividers)
/* ========================================================================== */
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Raleway', sans-serif;
	color: var(--bs-primary);
}
.dark .h1,
.dark .h2,
.dark .h3,
.dark .h4,
.dark .h5,
.dark .h6,
.dark h1,
.dark h2,
.dark h3,
.dark h4,
.dark h5,
.dark h6,
.color .h1 small,
.color .h2 small,
.color .h3 small,
.color .h4 small,
.color .h5 small,
.color .h6 small,
.color h1 small,
.color h2 small,
.color h3 small,
.color h4 small,
.color h5 small,
.color h6 small {
	color: #ffffff;
}
h1 .fa,
h2 .fa,
h3 .fa,
h4 .fa,
h5 .fa,
h6 .fa,
h1 .glyphicon,
h2 .glyphicon,
h3 .glyphicon,
h4 .glyphicon,
h5 .glyphicon,
h6 .glyphicon {
	color: #b30000;
}
.image h1 .fa,
.image h2 .fa,
.image h3 .fa,
.image h4 .fa,
.image h5 .fa,
.image h6 .fa,
.image h1 .glyphicon,
.image h2 .glyphicon,
.image h3 .glyphicon,
.image h4 .glyphicon,
.image h5 .glyphicon,
.image h6 .glyphicon,
.color h1 .fa,
.color h2 .fa,
.color h3 .fa,
.color h4 .fa,
.color h5 .fa,
.color h6 .fa,
.color h1 .glyphicon,
.color h2 .glyphicon,
.color h3 .glyphicon,
.color h4 .glyphicon,
.color h5 .glyphicon,
.color h6 .glyphicon {
	color: #ffffff;
}
a,
a .fa,
a .glyphicon,
a:hover,
a:hover .fa,
a:hover .glyphicon,
a:active,
a:focus {
	-webkit-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
	text-decoration: none;
}
a {
	&:not(.btn-theme) {
		color: var(--bs-primary);
	}
}
a:not(.btn-theme):hover,
a:not(.btn-theme):active,
a:not(.btn-theme):focus {
	color: #000000;
}
.footer {
	a {
		&:not(.btn-theme) {
			color: #14181c;
		}
	}
}
.footer a:not(.btn-theme):hover,
.footer a:not(.btn-theme):active,
.footer a:not(.btn-theme):focus {
	color: var(--bs-primary);
}
p {
	margin-bottom: 20px;
}
ul,
ol {
	padding-left: 0;
	list-style: none;
	margin-bottom: 20px;
}
ul ul,
ol ul,
ul ol,
ol ol {
	padding-left: 20px;
}
ul.list-check li,
ol.list-check li {
	padding-left: 20px;
	position: relative;
}
ul.list-check li:before,
ol.list-check li:before {
	content: '\f046';
	font-family: 'FontAwesome';
	position: absolute;
	top: 0;
	left: 0;
}
ul.list-check li + li,
ol.list-check li + li {
	margin-top: 7px;
}
ul {
	&.list-icons {
		li {
			padding-left: 30px;
			position: relative;
			& + li {
				margin-top: 10px;
			}
			& > .fa {
				position: absolute;
				left: 0;
				top: 3px;
			}
		}
	}
}
.section-title {
	margin-top: 0;
	margin-bottom: 25px;
	font-size: 30px;
	font-weight: 900;
	line-height: 30px;
	text-transform: uppercase;
	text-align: center;
	color: #14181c;
	overflow: hidden;
	small {
		display: block;
		font-size: 18px;
		font-weight: 400;
		line-height: 18px;
		text-transform: none;
		& + span {
			margin-top: 10px;
		}
	}
	span {
		display: block;
		& + small {
			margin-top: 10px;
		}
	}
	.thin {
		font-weight: 300 !important;
	}
	&.section-title-lg {
		font-size: 24px;
	}
	&.text-left {
		text-align: left;
	}
	&.text-right {
		text-align: right;
	}
	&.no-margin {
		margin: 0;
	}
}
.dark {
	.section-title {
		color: #ffffff;
		small {
			color: #ffffff;
		}
	}
}
.block-title {
	margin-top: 0;
	margin-bottom: 25px;
	font-size: 18px;
	font-weight: 900;
	line-height: 20px;
	text-transform: uppercase;
	color: #14181c;
	overflow: hidden;
	span {
		display: inline-block;
		position: relative;
		&:after {
			content: "";
			position: absolute;
			height: 1px;
			width: 2000px;
			background-color: #a5abb7;
			top: 10px;
			left: 100%;
			margin-left: 15px;
		}
	}
	.thin {
		font-weight: 300;
		margin-left: 5px;
	}
	&.alt {
		font-size: 16px;
		line-height: 24px;
		font-weight: 900;
		text-transform: uppercase;
		padding: 10px 70px 10px 20px;
		border: solid 1px #a5abb7;
		overflow: visible;
		position: relative;
		color: #7f7f7f;
		.fa {
			position: absolute;
			top: -1px;
			right: -1px;
			height: 46px;
			width: 46px;
			line-height: 45px;
			vertical-align: middle;
			text-align: center!important;
			background-color: #f5f6f8;
			border: solid 1px #a5abb7;
			color: #a5abb7;
			z-index: 20;
			&.color {
				background-color: var(--bs-primary);
			}
		}
	}
	&.alt2 {
		font-size: 16px;
		line-height: 24px;
		font-weight: 900;
		text-transform: uppercase;
		padding: 0 50px 20px 0;
		border-bottom: solid 1px #e9e9e9;
		overflow: visible;
		position: relative;
		color: #7f7f7f;
		.fa {
			position: absolute;
			top: -1px;
			right: -1px;
			height: 30px;
			width: 30px;
			line-height: 30px;
			vertical-align: middle;
			text-align: center!important;
			background-color: #e9e9e9;
			color: #a5abb7;
			z-index: 20;
			&.color {
				background-color: var(--bs-primary);
			}
		}
	}
}
blockquote {
	padding: 10px 20px;
	margin: 0 0 20px;	
}
.text-lg {
	text-transform: uppercase;
	font-size: 24px;
	line-height: 1.2;
	color: #9a0000;
}
.text-xl {
	font-size: 36px;
	line-height: 48px;
	font-weight: 300;
}
.text-thin {
	font-size: 11px;
	text-transform: uppercase;
	color: #14181c;
}
.text-dark {
	color: #14181c;
}
.text-color {
	color: var(--bs-primary);
}
.drop-cap {
	display: block;
	// float: left;
	font-size: 44px;
	line-height: 37px;
	margin: 0 10px 0 0;
	color: var(--bs-primary);
}

/* Blockquote
/* ======================================================================================== */
blockquote {
	background-color: var(--bs-primary);
	border: none;
	color: #ffffff;
	p {
		font-size: 30px;
		font-weight: 300;
		line-height: 36px;
		color: #ffffff;
	}
	footer {
		margin-top: 40px;
	}
}
blockquote footer,
  blockquote cite {
	font-size: 18px;
	font-style: italic;
	line-height: 20px;
	color: #ffffff;
}

  /* Page dividers
  /* ======================================================================================== */
  hr {
    &.page-divider {
      margin-top: 30px;
      margin-bottom: 30px;
      clear: both;
      border-color: #e9e9e9;
      &.half {
        border-color: transparent;
        margin-top: 0;
      }
      &.small {
        border-color: transparent;
        margin-top: 0;
        margin-bottom: 20px;
      }
      &.single {
        border-color: #d3d3d3;
      }
    }
  }
  hr.transparent,
    hr.page-divider.transparent {
    border-color: transparent;
  }
  ::-webkit-input-placeholder {
    color: #8f8f8f !important;
  }
  :-moz-placeholder {
    color: #8f8f8f !important;
  }
  ::-moz-placeholder {
    color: #8f8f8f !important;
  }
  :-ms-input-placeholder {
    color: #8f8f8f !important;
  }
  input {
    &:focus {
      &::-webkit-input-placeholder {
        color: transparent !important;
      }
      &:-moz-placeholder {
        color: transparent !important;
      }
      &::-moz-placeholder {
        color: transparent !important;
      }
      &:-ms-input-placeholder {
        color: transparent !important;
      }
    }
  }
  textarea {
    &:focus {
      &::-webkit-input-placeholder {
        color: transparent !important;
      }
      &:-moz-placeholder {
        color: transparent !important;
      }
      &::-moz-placeholder {
        color: transparent !important;
      }
      &:-ms-input-placeholder {
        color: transparent !important;
      }
    }
  }    